<script>
export default {
  name: "createStaff-component",
  data() {
    return {
      form: {
        p_Name: "", // 姓名
        sex: "", // 性別
        blood: "", // 血型
        birthday: "", // 生日
        country: "", // 國別
        email: "", // 電子郵件
        address: "", // 地址
        tel1: "", // 電話
        tel2: "", // 行動電話
        dept_Name: "", // 部門
        job_Title: "", // 職稱
        urgent_Contact: "", // 緊急聯絡人
        urgent_Tel: "", // 緊急聯絡人電話
        insurance_Begin: "",
        insurance_End: "",
        insurance_doc: "",
        id: "",
        s_Id: "",
      },
      rules: {
        p_Name: [{ required: true, message: "必填", trigger: "blur" }],
        sex: [{ required: true, message: "必填", trigger: "change" }],
        blood: [{ required: true, message: "必填", trigger: "change" }],
        birthday: [{ required: true, message: "必填", trigger: "change" }],
        country: [{ required: true, message: "必填", trigger: "change" }],
        email: [
          { required: false, message: "必填", trigger: "blur" },
          { type: "email", message: "信箱格式錯誤", trigger: "change" },
        ],
        address: [{ required: true, message: "必填", trigger: "blur" }],
        tel1: [{ required: true, message: "必填", trigger: "blur" }],
        tel2: [{ required: true, message: "必填", trigger: "blur" }],
        dept_Name: [{ required: false, message: "必填", trigger: "blur" }],
        job_Title: [{ required: false, message: "必填", trigger: "blur" }],
        urgent_Contact: [{ required: true, message: "必填", trigger: "blur" }],
        urgent_Tel: [{ required: true, message: "必填", trigger: "blur" }],

        insurance_Begin: [
          { required: false, message: "必填", trigger: "blur" },
        ],
        insurance_End: [{ required: false, message: "必填", trigger: "blur" }],
        insurance_doc: [{ required: false, message: "必填", trigger: "blur" }],
      },
    };
  },
  computed: {
    attachment() {
      return this.$store.state.attachment;
    },
    employeeDetail() {
      const data = this.$store.state.employeeDetail;
      return data;
    },
    newVendorInfo() {
      this.$store.state.newVendorInfo.forEach((e) => {
        e.label = `${e.name} ${e.e_Name}`;
      });
      return this.$store.state.newVendorInfo;
    },
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.modifyEmployeeInfo();
        } else {
          this.$message({
            type: "error",
            duration: 5000,
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    async modifyEmployeeInfo() {
      try {
        const res = await this.$store.dispatch("modifyEmployeeInfo", this.form);
        if (res.data.isOK) {
          this.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "修改成功",
          });
          this.$router.push({ name: "staffManagement" });
        }
      } catch (err) {
        console.log(err);
      }
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    async getEmployeeDetail(s_id, id) {
      try {
        await this.$store.dispatch("getEmployeeDetail", {
          s_id: s_id,
          id: id,
        });
        this.form = this.employeeDetail;
      } catch (err) {
        console.log(err);
      }
    },
    async getNewVendorInfo() {
      try {
        await this.$store.dispatch("getNewVendorInfo");
      } catch (err) {
        console.log(err);
      }
    },
  },
  async mounted() {
    const s_id = this.$route.params.s_id;
    const id = this.$route.params.id;
    await this.getEmployeeDetail(s_id, id);
    await this.getNewVendorInfo();

    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./editStaffProfile.html"></template>
